$border-settings: 1px solid black;
$c1: skyblue;
$c2: #D56EFF;
$c3: chartreuse;
$c4: orange;
$c5: red;
$c6: blue;
$cx: orange;
$gap: 10px;
$sebbe: 'https://www.onlygfx.com/wp-content/uploads/2019/02/7-noise-texture-overlays-1-768x589.png';
// $sebbe: 'https://www.onlygfx.com/wp-content/uploads/2019/02/7-noise-texture-overlays-4-768x538.png'

* {
    box-sizing: border-box; }

body {
    font-family: 'Roboto Slab', serif; }

.hidden {
    display: none !important; }

.to_the_depths {
    visibility: hidden !important;
    opacity: 0.5;
    transform: translateY(-10%); }
.blur {
    filter: blur(4px) !important; }

.w1-3 {
    width: 33.333333333% !important; }
.w2-3 {
    width: 66.666666666% !important; }
.w1-2 {
    width: 50% !important; }

#root {
    background-color: #151515;
    background-color: skyblue;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center; }

.container {
    width: 100%;
    max-width: 1920px;
    height: 100%;
    max-height: 1080px;
    display: flex;
    // flex-direction: column USED WITH TOP HEADER
    // overflow: hidden
 }    // border: 4px solid #373737
.header {
    background-color: #343434;
    width: 100%;
    display: block;
    padding: 5px;
    border-bottom: $border-settings;
    // display: none
    a {
        display: inline-block;
        text-decoration: none;
        color: white;
        margin: 5px 10px 8px;
        font-size: 14px;
        font-weight: 300;
        display: block;
        &.active {
            color: $c1; } } }
.location {
    background: transparent;
    width: 60px;
    // background-color: yellow
    display: flex;
    padding: $gap;
    gap: $gap;
    flex-direction: column;
    a {
        border: $border-settings;
        padding: 20px;
        height: 42px;
        border-radius: 7px;
        background: #252525;
        color: white;
        content: "";
        color: transparent;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
        &.active {
            border-color: $cx; } } }

.animated-page {
    position: absolute; }
.main {
    position: relative; }

.main, .animated-page {
    width: 100%;
    flex: 1;
    background-color: #303030;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    overflow: hidden;

    .sidebar {
        position: relative;
        background-color: honeydew;
        width: 25%;
        height: 100%;
        // padding: 20px 0px 20px 10px
        padding: 20px;
        // border-right: $border-settings
        overflow-x: hide;
        // overflow-y: scroll
        > *:first-child {
            margin-top: 0; } }
    .content {
        // padding: 20px
        width: 80%;
        height: 100%;
        background-color: #151515;
        // background-color: black
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        padding: $gap;
        gap: $gap;

        .cy {
            border: $border-settings;
            border-radius: 7px;
            background-color: #252525;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3); }

        .horizontal-split {
            height: 100%;
            width: 100%;
            display: flex;
            gap: $gap;
            flex-direction: column;
            position: relative; }

        .cc {
            width: 100%;
            height: 100%;
            background-color: #252525;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            gap: $gap;
            border: $border-settings;
            border-radius: 7px;
            border-color: $cx;
            gap: 0;
            background-color: #252525;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
            .cy {
                background-color: transparent;
                box-shadow: none;
                border: none; }
            >*:last-child {
                border: none !important; }

            // > *
            //     border-bottom: $border-settings
            // > *:last-child
 }            //     border-bottom: none

        .svg_popup {
            position: absolute;
            border: $border-settings;
            border-radius: 7px;
            width: 80%;
            left: 10%;
            height: 80%;
            top: 10%;
            background-color: #252525;
            z-index: 2;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
            // transition: filter 0.2s ease-in-out
            transition: 0.2s ease-in-out; } } }




.svg_container {
    width: 100%;
    height: 100%;
    position: relative;
    transition: filter 0.2s ease-in-out;
    .cy {
        width: 100%;
        height: 100%; }
    .svg_label {
        position: absolute;
        top: 7px;
        left: 7px;
        color: white;
        background-color: #101010;
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 14px;
        border: $border-settings;
        font-weight: 300; }

    .svg_counter {
        position: absolute;
        top: 45px;
        left: 5px;
        color: white;
        background-color: #101010;
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 18px;
        border: 2px solid black;
        &.valid {
            border-color: chartreuse; }
        &.invalid {
            border-color: black; } }

    &.interactive {
        circle {
            cursor: pointer; }
        .cy {
            border-color: $cx;
            border-width: 2px; } }
    &.active {
        // border-right: 3px solid green !important
        // border-left: 3px solid green !important
        // border-bottom: 1px solid green !important
        background-color: #151815;
        position: relative;
        background-color: transparent;
        overflow: hidden;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            // background-image: url($sebbe)
            animation: sebbe 30s infinite;
 }            // z-index: 0
        .cy {
            position: relative;
            z-index: 0; } }
    &.full svg {
        position: absolute;
        pointer-events: none;
        height: 100%;
        width: 300%;
        // background-color: rgba(255,0,0,0.1)
        // border: 2px solid red
        z-index: 1;
        circle {
            pointer-events: auto; } }

    &.full.top svg {
        top: 0;
        left: 0; }
    &.full.mid svg {
        top: 0;
        left: -100%; }
    &.full.bot svg {
        top: 0;
        left: -200%; }
    &.full {
        border-right: 2px dotted #050505; } }

@keyframes sebbe {
    0% {
        transform: scale(1); }
    50% {
        transform: scale(1.5); }
    100% {
        transform: scale(1); } }

.splitline {
    margin: 5px;
    padding: 10px;
    display: flex;
    font-size: 24px;
    border-bottom: 1px solid black;
    flex-direction: column;
    justify-content: space-between;
    &:hover {
        cursor: pointer; } }


.node-labels {
    pointer-events: none;
    font-size: 16px;
    // font-style: italic
    // font-family: 'Times New Roman', Times, serif
    font-weight: 400; }

circle, tspan, text {
    &.C1 {
        fill: $c1; }
    &.C2 {
        fill: $c2; }
    &.C3 {
        fill: $c3; }
    &.C4 {
        fill: $c4; }
    &.C5 {
        fill: $c5; }
    &.C6 {
        fill: $c6; }
    &.X {
        fill: $cx; } }

line {
    &.C1 {
        stroke: $c1; }
    &.C2 {
        stroke: $c2; }
    &.C3 {
        stroke: $c3; }
    &.C4 {
        stroke: $c4; }
    &.C5 {
        stroke: $c5; }
    &.C6 {
        stroke: $c6; }
    &.X {
        stroke: $cx; } }

.graph {
    .X {
        fill: $cx;
 } }        // color: white
circle.X {
    fill: #252525 !important;
    stroke: #303030;
 }    // stroke: #202020 !important
circle {
    stroke-width: 2px !important; }
